.container {
    width: 900px;
}

.card {
    background-color: #fff;
    border: none;
    border-radius: 10px;
    width: 250px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.image-container {
    position: relative;
}

.thumbnail-image {
    border-radius: 10px !important;
}

.discount {
    background-color: red;
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 4px;
    padding-right: 4px;
    font-size: 10px;
    border-radius: 6px;
    color: #fff;
}

.wishlist {
    height: 20px;
    width: 20px;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
}

.wishlist:hover {
    background-color: rgb(210, 59, 59);
}

.first {
    position: absolute;
    width: 100%;
    padding: 9px;
}

.dress-name {
    font-size: 13px;
    font-weight: bold;
    width: 75%;
}

.new-price {
    font-size: 13px;
    font-weight: bold;
    color: red;
}
.old-price {
    font-size: 8px;
    font-weight: bold;
    color: grey;
}
