.creme {
    background-color: #fff;
    border: 2px solid grey;
}
.creme:hover {
    border: 3px solid grey;
}

.creme:focus {
    background-color: grey;
}

.red {
    background-color: #fff;
    border: 2px solid red;
}

.red:hover {
    border: 3px solid red;
}
.red:focus {
    background-color: red;
}

.blue {
    background-color: #fff;
    border: 2px solid #40c4ff;
}

.blue:hover {
    border: 3px solid #40c4ff;
}
.blue:focus {
    background-color: #40c4ff;
}
.darkblue {
    background-color: #fff;
    border: 2px solid #01579b;
}
.darkblue:hover {
    border: 3px solid #01579b;
}
.darkblue:focus {
    background-color: #01579b;
}
.yellow {
    background-color: #fff;
    border: 2px solid #ffca28;
}
.yellow:hover {
    border-radius: 3px solid #ffca28;
}
.yellow:focus {
    background-color: #ffca28;
}

.item-size {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: #fff;
    border: 1px solid grey;
    color: grey;
    font-size: 10px;
    text-align: center;
    align-items: center;
    display: flex;
    justify-content: center;
}

.rating-star {
    font-size: 10px !important;
}
.rating-number {
    font-size: 10px;
    color: grey;
}
.buy {
    font-size: 16px;
    color: purple;
    font-weight: 500;
    cursor: pointer;
}
.buy:hover {
    font-size: 16px;
    color: black;
    font-weight: bold;
}

.discount-percent {
    font-size: 12px;
    font-weight: bold;
    position: relative;
    top: 5px;
}
.off {
    font-size: 14px;
    position: relative;
    bottom: 5px;
}
